body {
  font-family: var(--primaryFont);
  letter-spacing: 0.04em;
  color: var(--primaryTextColor);
  background-color: yellow;
}

h1,
h2,
h3,
ul,
p {
  padding: 0;
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}
